#sitemap {
  font-family: "Noto Sans Japanese", "Noto Sans", sans-serif;
  width: 90%;
  margin:0 auto;
  dt {
    font-weight: 700;
    @include font-line(17);
    border-bottom: 1px solid #ccc;
    margin-bottom: 0.5em;
  }
  dd {
    margin: 0 0 2em 0;
    padding:0;
  }
  ul {
    margin:0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
  }
  ul li {
    width: 50%;
    @include font-line(15,30);
    &:before {
      display: inline-block;
      content: "";
    }
    a {
      border: none;
      padding: 0;
      color: #000;
      text-decoration: none;
    }
    .fa-angle-right {
      margin-right: 3px;
    }
  }
}
#privacy-link {
  width: 100%!important;
}
.privacy{
  &__txt{
    padding: 10px;
		@include font-line(14);
  }
}